// позволяет менять контент для главной страницы блок "Купить онлайн" в Сочи и "Фильтры"
const initTabs = (btnClass, openBtnClass, blockClass, openBlockClass) => {
  const tabBtns = document.querySelectorAll('.' + btnClass);
  const tabBlocks = document.querySelectorAll('.' + blockClass);

  tabBtns.forEach((tabBtn) => {
    tabBtn.addEventListener('click', () => {
      tabBtns.forEach((btn) => {
        if (btn.classList.contains(openBtnClass)) {
          btn.classList.remove(openBtnClass);
        }
      });

      tabBtn.classList.add(openBtnClass);

      tabBlocks.forEach((tabBlock) => {
        if (tabBlock.classList.contains(openBlockClass)) {
          tabBlock.classList.remove(openBlockClass);
        }

        if (tabBtn.dataset.tab === tabBlock.dataset.tab) {
          tabBlock.classList.add(openBlockClass);
        }
      });
    });
  });
};

const tabs = () => {
  initTabs('promo__tab', 'promo__tab--active', 'promo__content', 'promo__content--show');
  initTabs('flat-online__tab', 'flat-online__tab--active', 'flat-online__block', 'flat-online__block--show');
};

export {tabs};
