// ввод только цифр
const getNumber = () => {
  let inputs = document.querySelectorAll('input[type="number"]');
  if (inputs.length > 0) {
    inputs.forEach((input) => {
      input.addEventListener('keydown', function (event) {
        if (event.keyCode >= 65 && event.keyCode <= 90) {
          event.returnValue = false;
        }
      });
    });
  }
};

export {getNumber};
