const filterBlocks = document.querySelectorAll('.filter-checkboxes--tags');

const filterTags = () => {
  if (filterBlocks) {
    filterBlocks.forEach((block) => {
      const btnsTags = block.querySelectorAll('[data-tags-btn]');
      const btnsSelectAll = block.querySelector('[data-select-all]');
      const btnsSelectList = block.querySelector('.filter-checkboxes__list');

      if (btnsTags) {
        btnsTags.forEach((btn) => {
          btn.addEventListener('click', () => {
            block.classList.toggle('filter-checkboxes--hide-tags');
          });
        });
      }

      if (btnsSelectAll && btnsSelectList) {
        const inputs = btnsSelectList.querySelectorAll('input[type="checkbox"]');

        if (inputs) {
          btnsSelectAll.addEventListener('click', () => {
            const isAllInputsChecked = Array(...inputs).every((input) => input.checked === true);

            inputs.forEach((input) => {
              if (isAllInputsChecked) {
                input.checked = false;
                return;
              }

              input.checked = true;
            });
          });
        }
      }
    });
  }
};

export {filterTags};
