// Открывает и закрывает списки
const dropdownLists = () => {
  let content = document.querySelectorAll('.dropdown-lists-content');
  let buttonLists = document.querySelectorAll('.details__title');


  // при ширине экрана больше 766px дизэйблид кнопки,
  // т.к. на desktop они просто текст
  buttonLists.forEach((element) => {
    if (window.matchMedia('(min-width: 766px)').matches) {
      element.setAttribute('disabled', 'true');
    } else {
      element.removeAttribute('disabled');
    }
  });

  // скрываем списки при включенном js
  content.forEach((element) => element.classList.add('dropdown-lists-none'));

  // Вешаем обработчик событий на каждый элемент
  document.querySelectorAll('.dropdown-lists-button').forEach((btn) => {
    btn.addEventListener('click', function () {
      // this текущая кнопка, где с помощью метода nextElementSibling
      // выбираем соседний элемент и добавляем в него css класс
      // eslint-disable-next-line no-invalid-this
      this.nextElementSibling.classList.toggle('dropdown-lists-active');
    });
  });
};

export {dropdownLists};
