import {disableScrolling, enableScrolling} from '../utils/scroll-lock';

const mobileMenu = () => {
  let hamburger = document.querySelector('.button-hamburger');
  let menu = document.querySelector('.header__menu');

  const toggleMenu = () => {
    menu.classList.toggle('header__menu-active');
    hamburger.classList.toggle('button-hamburger-active');

    if (menu.classList.contains('header__menu-active')) {
      disableScrolling();
    } else {
      enableScrolling();
    }
  };

  hamburger.addEventListener('click', (e) => {
    e.stopPropagation();

    toggleMenu();
  });

  document.addEventListener('click', (e) => {
    let target = e.target;
    let itsMenu = target === menu || menu.contains(target);
    let itsHamburger = target === hamburger;
    let menuIsActive = menu.classList.contains('header__menu-active');

    if (!itsMenu && !itsHamburger && menuIsActive) {
      toggleMenu();
    }
  });
};

export {mobileMenu};
