const swiperResize = (classSwiper, parametrs, onlyMobile, widthScreen) => {
  if (!document.querySelector(classSwiper)) {
    return;
  }

  let swiper;

  if (!onlyMobile) {
    // eslint-disable-next-line no-undef
    swiper = new Swiper(classSwiper, parametrs);
  } else {
    let mobile;
    const deleteInstance = true;
    const cleanStyles = true;

    if (window.innerWidth <= widthScreen) {
      mobile = true;
      // eslint-disable-next-line no-undef
      swiper = new Swiper(classSwiper, parametrs);
    }

    window.addEventListener('resize', function () {
      if (window.innerWidth <= widthScreen && mobile) {
        return;
      }
      if (window.innerWidth > widthScreen && !mobile) {
        return;
      }
      if (window.innerWidth > widthScreen && mobile) {
        mobile = false;
        if (swiper.destroy) {
          swiper.destroy(deleteInstance, cleanStyles);
        }
      }
      if (window.innerWidth <= widthScreen && !mobile) {
        mobile = true;
        // eslint-disable-next-line no-undef
        swiper = new Swiper(classSwiper, parametrs);
      }
    }, false);
  }

  document.addEventListener('DOMContentLoaded', function () {
    document.body.classList.add('ready');
  });
};

export {swiperResize};
