const checkOnInputSocials = () => {
  // на главной странице, в блоке бесплатная консультация позволяет
  // выбрать чекбокс при нажатии на иконку мессенджера
  let buttons = document.querySelectorAll('.socials__link');

  if (!buttons) {
    return;
  }

  buttons.forEach(function (btn, index) {
    btn.addEventListener('click', function () {
      let input = document.getElementById('id-socials-' + index);
      if (input.checked) {
        input.checked = false;
      } else {
        input.checked = true;
      }
    });
  });
};

export {checkOnInputSocials};
