const getPhone = () => {
  let buttonShowPhone = document.getElementById('show-phone-button');
  let phone = document.getElementById('show-phone-number');

  // если на странице нет phone, то не ищем
  if (!phone) {
    return;
  }

  // если js включен отображаем кнопку "Показать телефон"
  buttonShowPhone.classList.remove('js-show-phone');
  // и скрываем номер телефона
  phone.classList.add('js-hide-phone');

  // при клике на кнопку "Показать телефон" отображаем номер
  buttonShowPhone.addEventListener('click', function () {
    buttonShowPhone.classList.add('js-hide-phone');
    phone.classList.remove('js-hide-phone');
  });
};

export {getPhone};
