import {enableScrolling} from '../utils/scroll-lock';

// позволяет закрыть модальное окно "Еще фильтры" по клику на кнопку "Очистить"
// на страницах list-objects-small(big)
const closeMoreFilter = () => {
  let buttonClean = document.querySelector('.js-clean-more-filter');
  let buttonSave = document.querySelector('.js-save-more-filter');
  let modal = document.querySelector('.modal--filter');

  if (!buttonClean) {
    return;
  }

  buttonClean.addEventListener('click', function () {
    // при клике на кнопку "Очистить" очищает форму, а поля с choises очищает init-choises.js
    document.getElementById('modal-filter-more').reset();
  });

  const onCloseModal = () => {
    // закрывает модальное окно "Еще фильтры"
    modal.classList.remove('modal--active');
    // разершает скролл body
    enableScrolling();
  };

  // позволяет закрыть модальное окно "Еще фильтры" по клику на кнопку "Сохранить"
  buttonSave.addEventListener('click', onCloseModal);
};

export {closeMoreFilter};
