const checkInput = (classJS, inputId) => {
  const inputList = document.querySelector(classJS);
  if (!inputList) {
    return;
  }
  const inputs = inputList.querySelectorAll('input');
  const input = inputList.querySelector(inputId);

  inputList.addEventListener('change', function (evt) {
    if (evt.target === input) {
      for (let i = 1; i < inputs.length; i++) {
        inputs[i].checked = input.checked;
      }
    }
    if (evt.target.type === 'checkbox' && evt.target !== input) {
      if (input.checked === false) {
        return;
      }
      input.checked = false;
    }
  });
};

export {checkInput};
