// позволяет в list-objects-small(big) в фильтре "Время до моря" выбирать время
const getFilterTime = () => {
  // меняет "Время до моря" на выбранное значение "10 минут" и тп
  let span = document.querySelector('.filter-dropdown__span-time');
  let list = document.querySelector('.js-filter-time');
  let clearButton = document.querySelector('.modal-filter__btn--clear');
  let saveButton = document.querySelector('.js-filter-save');
  let tabInput = document.querySelectorAll('.js-time-choose-text');

  if (!span) {
    return;
  }

  // задает стартовое значение для массивов
  let arr = [];
  let times = ['10 минут'];
  let types = ['пешком'];

  tabInput.forEach(function (btn) {
    btn.addEventListener('click', function (evt) {
      let id = evt.target.getAttribute('id');
      let input = document.getElementById(id);
      let item = input.value;

      if (item === '10 минут' || item === '20 минут' || item === '40 минут') {
        times = [];
        times.push(item);
      }

      if (item === 'пешком' || item === 'на авто' || item === 'на общ. транспорте') {
        types = [];
        types.push(item);
      }

      // объединяет массивы "по времени" и "по транспорту"
      arr = times.concat(types);

      // выводит текст в кнопку
      span.innerHTML = '';
      span.classList.add('filter-dropdown__span-time-active');
      span.innerHTML = times[0] + '...' + '(' + arr.length + ')';
    });
  });

  // закрывает выпадающий список при нажатии на кнопку "Очистить"
  clearButton.addEventListener('click', function () {
    span.classList.remove('filter-dropdown__span-time-active');
    span.innerHTML = 'Время до моря';
    list.classList.remove('filter-dropdown--open');
  });

  // закрывает выпадающий список при нажатии на кнопку "Сохранить"
  // но оставляет выбранные пользователем данные
  saveButton.addEventListener('click', function () {
    list.classList.remove('filter-dropdown--open');
  });
};

export {getFilterTime};
