import {swiperResize} from './swiper-resize';

// слайдeр "Наши отзывы"
const reviewsSlider = () => {
  swiperResize('.reviews__swiper', {
    loop: true,
    centeredSlides: true,
    pagination: {
      el: '.swiper-pagination',
      type: 'fraction',
    },
    navigation: {
      nextEl: '.swiper-button-next',
      prevEl: '.swiper-button-prev',
    },
    slidesPerView: 1,
    spaceBetween: 15,
    breakpoints: {
      768: {
        slidesPerView: 2,
      },
      1028: {
        slidesPerView: 3,
      },
    },
  }, false, 0);
};

export {reviewsSlider};
