const filterDropdownInnerBlocks = document.querySelectorAll('.filter-dropdown-inner');

const filterDropdownInner = () => {
  if (filterDropdownInnerBlocks) {
    filterDropdownInnerBlocks.forEach((block) => {
      const btn = block.querySelector('.filter-dropdown-inner__btn');

      if (btn) {
        btn.addEventListener('click', () => {
          block.classList.toggle('filter-dropdown-inner--open');
        });
      }
    });
  }


};

export {filterDropdownInner};
