import {swiperResize} from './swiper-resize';

// слайдeр в about-company.html блок "Наша миссия"
const missionSlider = () => {
  swiperResize('.mission__slider', {
    slidesPerView: 1,
    pagination: {
      el: '.swiper-pagination',
      clickable: true,
    },
    navigation: {
      nextEl: '.swiper-button-next',
      prevEl: '.swiper-button-prev',
    },
  }, false, 0);
};

export {missionSlider};
