// модуль позволяет закрыть блок с email при нажатии на крестик
// находится на странице news.html и  peice-news.html
const getCloseBlockEmail = () => {
  let button = document.querySelector('.banner-news__close-btn');
  let block = document.querySelector('.js-banner-news');

  if (!button) {
    return;
  }

  button.addEventListener('click', function () {
    block.classList.add('js-banner-news-no-active');
  });
};

export {getCloseBlockEmail};
