import {width} from '../utils/variables';

const setMinHeightOfTimeline = () => {
  const $timeline = document.querySelector('.js-history-timeline');

  if (window.innerWidth > width.tabletCenter) {
    if ($timeline) {
      const $$timelineMilestones = document.querySelectorAll('.js-history-timeline-milestone');


      if ($$timelineMilestones.length > 0) {
        let zeroSideMaxHeight = 0;
        let oneSideMaxHeight = 0;

        $$timelineMilestones.forEach(($timelineMilestone) => {
          const $timelineMilestoneTextWrapper = $timelineMilestone.querySelector('.js-history-timeline-milestone-text-wrapper');

          if ($timelineMilestoneTextWrapper) {
            const currentHeight = $timelineMilestoneTextWrapper.getBoundingClientRect().height;


            if ($timelineMilestone.classList.contains('js-history-timeline-milestone-zero-side') && currentHeight > zeroSideMaxHeight) {
              zeroSideMaxHeight = currentHeight;

            }

            if ($timelineMilestone.classList.contains('js-history-timeline-milestone-one-side') && currentHeight > oneSideMaxHeight) {
              oneSideMaxHeight = currentHeight;

            }
          }
        });

        const sumOfMaxHeights = zeroSideMaxHeight + oneSideMaxHeight;

        $timeline.style = `min-height: ${sumOfMaxHeights}px`;
      }
    }
  } else {
    if ($timeline) {
      $timeline.style.removeProperty('min-height');
    }
  }
};

export const historyTimeline = () => {
  document.addEventListener('DOMContentLoaded', () => {
    setMinHeightOfTimeline();
  });

  window.addEventListener('orientationchange', () => {
    setMinHeightOfTimeline();
  });

  window.addEventListener('resize', () => [
    setMinHeightOfTimeline()
  ]);
};
