// Слайдер для list-objects-big.html карточек квартир с изображениями
const cardsSwiper = () => {
  const sliderMain = document.querySelectorAll('.slider-gallery__main');
  const sliderNav = document.querySelectorAll('.slider-gallery__thumbs');

  if (!sliderMain.length) return

  let mainArray = [];
  let navArray = [];

  sliderNav.forEach(function (element, i) {
    navArray.push(
      new Swiper(element, {
        slidesPerView: 5,
        spaceBetween: 10,
        loop: true,
        direction: 'vertical',
        loopedSlides: 5,
        watchSlidesVisibility: true,
        watchSlidesProgress: true,
      }),
    )

    sliderMain.forEach(function (element, i) {
      mainArray.push(
        new Swiper(element, {
          spaceBetween: 0,
          loop: true,
          direction: 'vertical',
          thumbs: {
            swiper: navArray[i],
          },
        })
      );
    });
  });
};

export {cardsSwiper};
