const accMenu = () => {
  let navigation = document.querySelector('.navigation__open-mobile');
  let accBtnDown = document.querySelector('.navigation__more');
  let accBtnUp = document.querySelector('.navigation__more-top');
  let listEstate = document.querySelector('.navigation__list--estate');
  let listCompany = document.querySelector('.navigation__list--company');
  let social = document.querySelector('.navigation__social');

  // если js отключен
  navigation.classList.remove('navigation__no-js');
  listCompany.classList.add('navigation__list--acc-none');
  social.style.display = 'none';

  const openUp = () => {
    listEstate.classList.add('navigation__list--acc-none');
    listCompany.classList.remove('navigation__list--acc-none');
    accBtnDown.classList.add('navigation__list--acc-none');
    accBtnUp.classList.remove('navigation__more-none');
    social.style.display = 'block';
  };

  accBtnDown.addEventListener('click', openUp);

  const openDown = () => {
    listEstate.classList.remove('navigation__list--acc-none');
    listCompany.classList.add('navigation__list--acc-none');
    accBtnDown.classList.remove('navigation__list--acc-none');
    accBtnUp.classList.add('navigation__more-none');
    social.style.display = 'none';
  };

  accBtnUp.addEventListener('click', openDown);
};

export {accMenu};
