const consultSlider = () => {
  // eslint-disable-next-line no-undef, no-unused-vars
  const swiper = new Swiper('.promo__tabs', {
    navigation: {
      nextEl: '.promo__nav-btn--next',
      prevEl: '.promo__nav-btn--prev',
    },
    freeMode: true,
    slideClass: 'promo__tab',
    slidesPerView: 'auto',
  });

  const filterBlocks = document.querySelectorAll('.promo-filter');

  if (filterBlocks) {
    filterBlocks.forEach((block) => {
      const btn = block.querySelector('.promo-filter__more');

      if (btn) {
        btn.addEventListener('click', () => {
          block.classList.toggle('promo-filter--open');
        });
      }
    });
  }

  const initSwiperConsult = () => {
    // if убирает ошибку из консоли с других страниц
    if (!document.querySelector('.consult__slider')) {
      return;
    } else {
      // eslint-disable-next-line no-undef
      const swiper2 = new Swiper('.consult__slider', {
        slideClass: 'consult__slide',
        slidesPerView: 1,
        watchSlidesVisibility: true,
        watchSlidesProgress: true,
        slideToClickedSlide: true,
        spaceBetween: -100,
        loop: true,
        loopAdditionalSlides: 2,
        // slidesOffsetBefore: 10,
        // slidesOffsetAfter: 10,
        navigation: {
          nextEl: '.consult__nav--next',
          prevEl: '.consult__nav--prev',
        },

        breakpoints: {
          768: {
            slidesPerView: 3,
            spaceBetween: -120,
            // simulateTouch: false,
            centeredSlides: true,
            allowTouchMove: false,
            loop: false,
          },
        },
      });

      const consultSlides = document.querySelectorAll('.consult__slide');

      const deactiveSlides = () => {
        consultSlides.forEach((slide) => {
          slide.classList.remove('consult__slide--active');
          slide.classList.remove('consult__slide--pre-active');
        });
      };

      const activeFirstSlide = () => {
        deactiveSlides();
        consultSlides[0].classList.add('consult__slide--active');
      };

      document.querySelectorAll('.consult__btns-to').forEach((btn) => {
        btn.addEventListener('click', () => {
          if (!(btn.dataset.slide === 0) || !(btn.dataset.slide === (consultSlides.length - 1))) {
            deactiveSlides();
          }

          swiper2.slideTo(btn.dataset.slide);
        });
      });

      swiper2.slideTo(1);
      activeFirstSlide();

      swiper2.on('slideChange', function () {
        if (swiper2.activeIndex === 0) {
          swiper2.slideTo(1);
          consultSlides[0].classList.add('consult__slide--active');
          return;
        }

        if (swiper2.activeIndex === (consultSlides.length - 1)) {
          swiper2.slideTo(consultSlides.length - 2);
          consultSlides[consultSlides.length - 2].classList.add('consult__slide--pre-active');
          consultSlides[consultSlides.length - 1].classList.add('consult__slide--active');
          return;
        }

        deactiveSlides();
      });

      consultSlides[1].addEventListener('click', () => {
        deactiveSlides();
      });

      consultSlides[consultSlides.length - 2].addEventListener('click', () => {
        deactiveSlides();
      });
    }
  };

  initSwiperConsult();
};

export {consultSlider};
