import {swiperResize} from './swiper-resize';

// слайдeр в about-company.html блок "О компании"
const navigationSlider = () => {
  swiperResize('.hero__navigation', {
    freeMode: true,
    slideClass: 'hero__item-navigation',
    slidesPerView: 'auto',
    spaceBetween: 15,
    navigation: {
      nextEl: '.hero__control--button-next',
      prevEl: '.hero__control--button-prev',
    },
  }, true, 767);
};

export {navigationSlider};
