/* eslint-disable new-cap */
const cardFlatSlider = () => {
  let sliders = document.querySelectorAll('.js-card-flat-swiper');
  // eslint-disable-next-line no-undef, no-unused-vars
  let cardSlider = new Swiper(sliders, {
    // Буллеты (навигация)
    pagination: {
      el: '.swiper-pagination',
      clickable: true,
      renderBullet(index, className) {
        return '<span class="' + className + '">' + '<span class="card-flat__bullet-line"></span>' + '</span>';
      },
    },
    // Активный слайд по центру
    centeredSlides: true,
    // Бесконечный слайдер
    loop: true,
    // Скорость
    speed: 1000,
    // Сколько слайдов показывать
    slidesPerView: 1,
  });

  // событие click срабатывает без участия пользователя
  function trigger(target, eventType) {
    let event = document.createEvent('MouseEvents');
    event.initEvent(eventType, true, true);
    target.dispatchEvent(event);
  }

  let bullet = document.querySelectorAll('.swiper-pagination-bullet');
  // при hover на bullet меняет изображение
  bullet.forEach(function (btn) {
    btn.addEventListener('mouseenter', function () {
      // eslint-disable-next-line no-invalid-this
      trigger(this, 'click');
    });
    btn.addEventListener('mouseleave', function () {
      // eslint-disable-next-line no-invalid-this
      trigger(this, 'click');
    });
  });
};

export {cardFlatSlider};
