const dropdown = () => {
  const initDropdown = (blockClass, blockActiveClass, btnClass, overlayClass) => {
    const dropdowns = document.querySelectorAll('.' + blockClass);

    // const hideDropdownBlocks = () => {
    //   dropdowns.forEach((dropdownBlock) => {
    //     dropdownBlock.querySelector('.dropdown__btn').classList.remove('dropdown__btn--active');
    //     dropdownBlock.querySelector('.dropdown__content').classList.remove('dropdown__content--show');
    //   });

    //   document.removeEventListener('click', hideDropdownBlocks);
    // };
    let activeBtn;

    dropdowns.forEach((block) => {
      const dropdownsBtn = block.querySelector('.' + btnClass);
      const overlay = block.querySelector('.' + overlayClass);

      dropdownsBtn.addEventListener('click', () => {
        if (activeBtn !== dropdownsBtn) {
          dropdowns.forEach((el) => {
            el.classList.remove(blockActiveClass);
          });
        }

        block.classList.toggle(blockActiveClass);

        activeBtn = dropdownsBtn;
      });

      overlay.addEventListener('click', () => {
        block.classList.remove(blockActiveClass);
      });
    });
  };

  initDropdown('filter-dropdown', 'filter-dropdown--open', 'filter-dropdown__btn', 'filter-dropdown__overlay');
};

export {dropdown};
