import {setupModal} from '../utils/modal';

const modals = document.querySelectorAll('.modal');
const modalFilter = document.querySelector('.modal--filter');
const modalFilterBtns = document.querySelectorAll('[data-modal="filter"]');
const modalSearchMap = document.querySelector('.modal--search-map');
const modalSearchMapBtns = document.querySelectorAll('[data-modal="search-map"]');
// const modalSuccess = document.querySelector('.modal--success');
// const modalSuccessBtns = document.querySelectorAll('[data-modal="success"]');

// аргументы setupModal(modal, closeCallback, modalBtns, openCallback, noPrevDefault, preventScrollLock)
// возможна инициализация только с первыми аргументом,
// если вам нужно открывать модалку в другом месте под какими-нибудь условиями
const initModals = () => {
  // фикс для редких случаев, когда модалка появляется при загрузке страницы
  window.addEventListener('load', () => {
    if (modals.length) {
      modals.forEach((el) => {
        setTimeout(() => {
          el.classList.remove('modal--preload');
        }, 100);
      });
    }
  });

  if (modalFilter && modalFilterBtns.length) {
    setupModal(modalFilter, false, modalFilterBtns, false, false);
  }
  if (modalSearchMap && modalSearchMapBtns.length) {
    setupModal(modalSearchMap, false, modalSearchMapBtns, false, false);
  }
  // if (modalSuccess && modalSuccessBtns.length) {
  //   setupModal(modalSuccess, false, modalSuccessBtns);
  // }
};

export {initModals};
